<template>
    <div class="card card-map">
      <div class="header">
        <h4 class="title">Localización del Paciente</h4>
      </div>
      <div class="map">
        <div id="map"></div>
      </div>
    </div>
</template>
<script>
  export default {
    props: ['alert'],
    mounted () {
      setTimeout(() => {
        this.loadMap(this.alert.latitud, this.alert.longitud);
      }, 1000);
    },
    methods: {
      loadMap: function (latitud, longitud) {
        var myLatlng = new window.google.maps.LatLng(latitud, longitud);
        var mapOptions = {
          zoom: 14,
          center: myLatlng,
          scrollwheel: false, // we disable de scroll over the map, it is a really annoing when you scroll through page
          styles: [{
            'featureType': 'water',
            'stylers': [{ 'saturation': 43 }, { 'lightness': -11 }, { 'hue': '#0088ff' }]
          }, {
            'featureType': 'road',
            'elementType': 'geometry.fill',
            'stylers': [{ 'hue': '#ff0000' }, { 'saturation': -100 }, { 'lightness': 99 }]
          }, {
            'featureType': 'road',
            'elementType': 'geometry.stroke',
            'stylers': [{ 'color': '#808080' }, { 'lightness': 54 }]
          }, {
            'featureType': 'landscape.man_made',
            'elementType': 'geometry.fill',
            'stylers': [{ 'color': '#ece2d9' }]
          }, {
            'featureType': 'poi.park',
            'elementType': 'geometry.fill',
            'stylers': [{ 'color': '#ccdca1' }]
          }, {
            'featureType': 'road',
            'elementType': 'labels.text.fill',
            'stylers': [{ 'color': '#767676' }]
          }, {
            'featureType': 'road',
            'elementType': 'labels.text.stroke',
            'stylers': [{ 'color': '#ffffff' }]
          }, { 'featureType': 'poi', 'stylers': [{ 'visibility': 'off' }] }, {
            'featureType': 'landscape.natural',
            'elementType': 'geometry.fill',
            'stylers': [{ 'visibility': 'on' }, { 'color': '#b8cb93' }]
          }, { 'featureType': 'poi.park', 'stylers': [{ 'visibility': 'on' }] }, {
            'featureType': 'poi.sports_complex',
            'stylers': [{ 'visibility': 'on' }]
          }, { 'featureType': 'poi.medical', 'stylers': [{ 'visibility': 'on' }] }, {
            'featureType': 'poi.business',
            'stylers': [{ 'visibility': 'simplified' }]
          }]
        }
        var map = new window.google.maps.Map(document.getElementById('map'), mapOptions)
        var marker = new window.google.maps.Marker({
          position: myLatlng,
          title: 'Paciente'
        })
        // To add the marker to the map, call setMap();
        marker.setMap(map)
      }
    },
    watch: {
      alert: function (newVal, oldVal) {
        this.loadMap(newVal.latitud, newVal.longitud)
      }
    }

  }
</script>
<style>
</style>



